import React, { Component } from 'react'
import { connect } from 'react-redux'
import styled from 'styled-components'
import $ from 'jquery'
import Loader from './Loader'
import Functions from '../functions/Functions'

class Reestr extends Component {
  constructor(props){
    super(props);
    this.state = {
      visible: 1,
      loading: true,
    }
    this.getRenters = this.getRenters.bind(this);
  }

  //       
  async componentDidMount() {
    var self = this;
    this.setState({loading: true})

    //  
    await $.ajax({ 
      url: `${window.location.hostname === 'localhost' ? 'http://arenda.local' : window.location.origin}/api/renters/read.php`,
      type: 'POST',
      success: function(res) {
        self.props.getRenters(res)
      },
      error: function(err) {
        console.log(err);
      }
    });

    const formData = new FormData();
    formData.append('visible', this.state.visible);
    //  
    await $.ajax({ 
      url: `${window.location.hostname === 'localhost' ? 'http://arenda.local' : window.location.origin}/api/contract/read.php`,
      data        : formData,
      processData : false,
      contentType : false,
      type: 'POST',
      success: function(res) {
        self.props.getContracts(res)
      },
      error: function(err) {
        console.log(err);
      }
    });

    //    
    await (function() {
      self.setState({ loading: false });
      const F = new Functions();
      F.hideRenters(); //    
    })();
  }

  async getRenters(e) {
    //   (     " ")
    e.preventDefault(); 
    var self = this;
    this.setState({loading: true})
    this.state.visible === 1 ? this.setState({ visible: 0 }) : this.setState({ visible: 1 });
    await $.ajax({ 
      url: `${window.location.hostname === 'localhost' ? 'http://arenda.local' : window.location.origin}/api/renters/read.php`,
      type: 'POST',
      success: function(res) {
        self.props.getRenters(res)
      },
      error: function(err) {
        console.log(err);
      }
    });

    const formData = new FormData();
    formData.append('visible', this.state.visible);
    //  
    await $.ajax({ 
      url: `${window.location.hostname === 'localhost' ? 'http://arenda.local' : window.location.origin}/api/contract/read.php`,
      data        : formData,
      processData : false,
      contentType : false,
      type: 'POST',
      success: function(res) {
        self.props.getContracts(res)
      },
      error: function(err) {
        console.log(err);
      }
    });
    
    //    
    await (function() {
      self.setState({ loading: false });
      const F = new Functions();
      F.hideRenters(); //    
    })();
  }

  // Toggle      
  toggleRenterDetails = e => $(e.target).siblings('.renter-details').slideToggle();
  toggleContractDetails = e => $(e.target).siblings('.details').slideToggle();
  
  render() {
    const store = this.props.testStore;
    const { visible, loading } = this.state;

    return (
      <div className='container'>
        {/*  loading===true  ,    */}
        {loading ? <Loader></Loader> : (<>
        
        <div>
          <button onClick={this.getRenters} className='btn' style={{ 'marginBottom':'30px'}} id='showAllbtn'>
           {!visible ? '   ' : '  '}
          </button>
        </div>

        {/*   ( smarty foreach) */}
        {store.renters.map((renter, i) => {                  
          return (  
          <RenterContainer key={`renter-${i}`} onClick={this.toggleRenterDetails} className='renter'>   
            <h2>{renter.short_name}  : 
              {renter.balance >= 0 ? <span style={{'color':'green'}}> {renter.balance}  </span> : <span style={{'color':'red'}}> {renter.balance} </span>}
            </h2>
            <RenterDetails style={{'display':'none'}} className='renter-details'>
              <p><b>:</b> {renter.inn} / <b>:</b> {renter.kpp}</p>
              <p><b>/:</b> {renter.ogrn}</p>
              <p><b> :</b> {renter.uridich_address}</p>
              <p><b> :</b> {renter.post_adress}</p>
              <p><b>:</b> {renter.phone}</p>
              <p><b>E-mail:</b> {renter.email}</p>
              <p><b> :</b> {renter.bank_name}</p>
              <p><b>:</b> {renter.bank_bik}</p>
              <p><b>. :</b> {renter.bank_ks}</p>
              <p><b> :</b> {renter.bank_rs}</p>
            </RenterDetails>

            {/*   */}
            {store.contracts.map((contract, j) => {
              if (contract.renter_id === renter.id) {

                return (
                  <RenterContract data-status={contract.status} key={`contract-${j}`} className='renter-contract'>
                    <RenterContractDetails onClick={this.toggleContractDetails}>
                      
                      {contract.end_date !== '' 
                        ? <p>{contract.contract_number}  {contract.datetime}</p> 
                        : <p>{contract.contract_number}  {contract.datetime} <i>()</i></p>
                      }

                      {contract.status === '0' 
                        ? <span style={{'color':'red'}}></span> 
                        : contract.status === '0.5'
                        ? <span style={{'color':'yellow'}}> : {contract.end_date}</span>
                        : <span style={{'color':'green'}}></span>
                      }
                    </RenterContractDetails>
                      <div style={{'display':'none'}} className='details'>
                        <p><b>:</b> {contract.room_number}</p>
                        <p><b>  :</b> {contract.number_scheme}</p>
                        <p><b>:</b> {contract.square}</p>
                        <p><b> :</b> {contract.summa}</p>
                        <p><b>  :</b> {contract.discoint}</p>
                        <p><b> :</b>  {contract.start_date}  {contract.end_date}</p>
                        <p><b> :</b> {contract.peni}</p>
                        <p><b>  :</b> {contract.start_date}</p>
                      </div>
                  </RenterContract>
                )
              }   
            })}
          </RenterContainer>
          ) 
        })} 
        </>)}
      </div> 
    )
  }
}

const RenterContainer = styled.div`
  width: 700px;
  margin: 0 auto;
  text-align: center;
  h2 {
    font-size: 18px;
    padding: 10px 0;
    margin: 0;
    cursor: pointer;
    border-bottom: 1px solid #2323;
    :hover {
      background: lightblue;
      transition: .37s ease;
    }
  }
`;

const RenterDetails = styled.div`
  p {
    text-align: left;
    font-size: 14px;
    line-height: 7px;
  }
`;

const RenterContract = styled.div`
  p {
    text-align: left;
    font-size: 12px;
    line-height: 7px;
  }
`;

const RenterContractDetails = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  padding: 0 10px;
  :hover {
    background: lightblue;
    transition: .37s ease;
  }
  div {
    padding: 0 10px;
  }
  p {
    font-size: 14px;
    pointer-events: none;
  }
`;

export default connect(
  state => ({
    testStore: state
  }),
  dispatch => ({
    getContracts: (contracts) => {
      dispatch({ type: 'FETCH_CONTRACTS', payload: contracts})
    },
    getRenters: (renters) => {
      dispatch({ type: 'FETCH_RENTERS', payload: renters})
    },
    getRooms: (rooms) => {
      dispatch({ type: 'FETCH_ROOMS', payload: rooms})
    }      
  })
)(Reestr);