import $ from 'jquery';

class Functions {
  hideRenters() {
    if (window.location.pathname === '/reestr') {
      $('.renter').each(function () {
        if ($(this).children(".renter-contract").length > 0) {
          return;
        } else {
          $(this).hide();
        }
      });
    }
  }

  getNumberOfMonth(month) {
    let monthNumber = '00';
    switch(month) {
      case '':
        monthNumber = '01'; 
        break; 
      case '':
        monthNumber = '02'; 
        break;
      case '':
        monthNumber = '03'; 
        break;
      case '':
        monthNumber = '04'; 
        break; 
      case '':
        monthNumber = '05'; 
        break;
      case '':
        monthNumber = '06'; 
        break;
      case '':
        monthNumber = '07';  
        break;
      case '':
        monthNumber = '08'; 
        break;
      case '':
        monthNumber = '09'; 
        break;
      case '':
        monthNumber = '10';  
        break;
      case '':
        monthNumber = '11'; 
        break;
      case '':
        monthNumber = '12';         
        break;
      default: 
    }  
    return monthNumber; 
  }

  getStringOfMonth(month) {
    let monthNumber = '00';
    switch(month) {
      case '01':
        monthNumber = ''; 
        break; 
      case '02':
        monthNumber = ''; 
        break;
      case '03':
        monthNumber = ''; 
        break;
      case '04':
        monthNumber = ''; 
        break; 
      case '05':
        monthNumber = ''; 
        break;
      case '06':
        monthNumber = ''; 
        break;
      case '07':
        monthNumber = '';  
        break;
      case '08':
        monthNumber = ''; 
        break;
      case '09':
        monthNumber = ''; 
        break;
      case '10':
        monthNumber = '';  
        break;
      case '11':
        monthNumber = ''; 
        break;
      case '12':
        monthNumber = '';         
        break;
      default: 
    }  
    return monthNumber; 
  }

  setCookie(cname, cvalue, exdays) {
    var d = new Date();
    d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
    var expires = "expires="+d.toUTCString();
    document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
  }

  getCookie(cname) {
    var name = cname + "=";
    var ca = document.cookie.split(';');
    for(var i = 0; i < ca.length; i++) {
      var c = ca[i];
      while (c.charAt(0) === ' ') {
        c = c.substring(1);
      }
      if (c.indexOf(name) === 0) {
        return c.substring(name.length, c.length);
      }
    }
    return "";
  }

  checkCookie() {
    var user = this.getCookie("username");
    if (user !== "") {
      alert("Welcome again " + user);
    } else {
      user = prompt("Please enter your name:", "");
      if (user !== "" && user != null) {
        this.setCookie("username", user, 365);
      }
    }
  }
} 

export default Functions;